<template>
  <div id="app">
    <header class="app-header">
      <div class="container">
        <div class="header-content">
          <div class="logo">
            <i class="fas fa-images"></i>
            <h1>Vue图片画廊</h1>
          </div>
          <div class="header-actions">
            <DataManagement />
            <div class="upload-btn">
              <button @click="openUploadModal" class="btn-primary">
                <i class="fas fa-cloud-upload-alt"></i> 上传图片
              </button>
            </div>
          </div>
        </div>
      </div>
    </header>
    
    <main class="app-main">
      <router-view />
    </main>
    
    <footer class="app-footer">
      <div class="container">
        <p>© Vue图片画廊应用 | Vue3图片画廊大作业 | 数据自动保存到本地存储</p>
      </div>
    </footer>
    
    <!-- 全局上传模态框 -->
    <ImageUploadModal 
      v-if="showUploadModal" 
      @close="closeUploadModal" 
      @upload-success="handleUploadSuccess"
    />
  </div>
</template>

<script>
import { ref } from 'vue'
import ImageUploadModal from './components/ImageUploadModal.vue'
import DataManagement from './components/DataManagement.vue'
import { useImageStore } from './stores/imageStore'

export default {
  name: 'App',
  components: {
    ImageUploadModal,
    DataManagement
  },
  setup() {
    const showUploadModal = ref(false)
    const imageStore = useImageStore()
    
    const openUploadModal = () => {
      showUploadModal.value = true
    }
    
    const closeUploadModal = () => {
      showUploadModal.value = false
    }
    
    const handleUploadSuccess = (imageData) => {
      imageStore.addImage(imageData)
      closeUploadModal()
      alert('图片上传成功！数据已保存到本地存储。')
    }
    
    return {
      showUploadModal,
      openUploadModal,
      closeUploadModal,
      handleUploadSuccess
    }
  }
}
</script>

