<template>
  <div class="home">
    <div class="container">
      <!-- 添加上传成功提示 -->
      <div v-if="showUploadSuccess" class="notification success">
        <i class="fas fa-check-circle"></i>
        <span>图片上传成功！数据已保存到本地存储。</span>
        <button class="close-notification" @click="showUploadSuccess = false">
          <i class="fas fa-times"></i>
        </button>
      </div>
      
      <!-- 添加编辑成功提示 -->
      <div v-if="showEditSuccess" class="notification info">
        <i class="fas fa-info-circle"></i>
        <span>图片编辑成功！数据已保存到本地存储。</span>
        <button class="close-notification" @click="showEditSuccess = false">
          <i class="fas fa-times"></i>
        </button>
      </div>
      
      <!-- 分类筛选 -->
      <section class="categories-section">
        <h2 class="section-title">图片分类</h2>
        <div class="categories">
          <button 
            v-for="category in categories" 
            :key="category"
            :class="['category-btn', { active: selectedCategory === category }]"
            @click="selectCategory(category)"
          >
            {{ category }}
          </button>
        </div>
      </section>

      <!-- 图片画廊 -->
      <section class="gallery-section">
        <div class="gallery-header">
          <h2 class="section-title">图片画廊</h2>
          <div class="gallery-info">
            <span class="image-count">{{ filteredImages.length }} 张图片</span>
            <span class="storage-info" @click="showStorageInfo">
              <i class="fas fa-database"></i> 本地存储
            </span>
            <div class="view-toggle">
              <button :class="['view-btn', { active: viewMode === 'grid' }]" @click="viewMode = 'grid'">
                <i class="fas fa-th-large"></i>
              </button>
              <button :class="['view-btn', { active: viewMode === 'list' }]" @click="viewMode = 'list'">
                <i class="fas fa-list"></i>
              </button>
            </div>
          </div>
        </div>

        <div :class="['gallery', viewMode]">
          <div 
            v-for="image in filteredImages" 
            :key="image.id"
            class="image-item"
            @click="previewImage(image)"
          >
            <div class="image-wrapper">
              <img 
                :src="image.url" 
                :alt="image.title"
                :data-src="image.url"
                :class="['gallery-image', image.filter]"
                loading="lazy"
              />
              <div class="image-overlay">
                <div class="image-actions">
                  <button class="action-btn" @click.stop="editImage(image)" title="编辑">
                    <i class="fas fa-edit"></i>
                  </button>
                  <button class="action-btn" @click.stop="deleteImage(image.id)" title="删除">
                    <i class="fas fa-trash"></i>
                  </button>
                  <button class="action-btn" @click.stop="applyFilterToImage(image.id, 'grayscale')" title="灰度滤镜">
                    <i class="fas fa-adjust"></i>
                  </button>
                </div>
              </div>
            </div>
            <div class="image-info">
              <h3 class="image-title">{{ image.title }}</h3>
              <p class="image-description">{{ image.description }}</p>
              <div class="image-meta">
                <span class="image-category">{{ image.category }}</span>
                <span class="image-date">{{ image.createdAt }}</span>
              </div>
            </div>
          </div>
        </div>
        
        <!-- 空状态 -->
        <div v-if="filteredImages.length === 0" class="empty-state">
          <i class="fas fa-images"></i>
          <h3>暂无图片</h3>
          <p>点击顶部"上传图片"按钮添加第一张图片</p>
        </div>
      </section>
    </div>

    <!-- 图片预览模态框 -->
    <ImagePreviewModal 
      v-if="currentPreviewImage" 
      :image="currentPreviewImage"
      @close="closePreview"
      @edit="editCurrentImage"
      @apply-filter="applyFilterToCurrentImage"
    />

    <!-- 图片编辑模态框 -->
    <ImageEditModal 
      v-if="currentEditImage" 
      :image="currentEditImage"
      @close="closeEdit"
      @save="saveImage"
    />
  </div>
</template>

<script>
import { computed, ref } from 'vue'
import { useImageStore } from '../stores/imageStore'
import ImagePreviewModal from '../components/ImagePreviewModal.vue'
import ImageEditModal from '../components/ImageEditModal.vue'

export default {
  name: 'Home',
  components: {
    ImagePreviewModal,
    ImageEditModal
  },
  setup() {
    const imageStore = useImageStore()
    
    const viewMode = ref('grid')
    const showUploadSuccess = ref(false)
    const showEditSuccess = ref(false)
    
    const categories = computed(() => imageStore.categories)
    const selectedCategory = computed(() => imageStore.selectedCategory)
    const filteredImages = computed(() => imageStore.filteredImages)
    const currentPreviewImage = computed(() => imageStore.currentPreviewImage)
    const currentEditImage = computed(() => imageStore.currentEditImage)
    
    const selectCategory = (category) => {
      imageStore.selectedCategory = category
    }
    
    const previewImage = (image) => {
      imageStore.setPreviewImage(image)
    }
    
    const closePreview = () => {
      imageStore.clearPreviewImage()
    }
    
    const editImage = (image) => {
      imageStore.setEditImage(image)
    }
    
    const editCurrentImage = () => {
      if (currentPreviewImage.value) {
        imageStore.setEditImage(currentPreviewImage.value)
        imageStore.clearPreviewImage()
      }
    }
    
    const closeEdit = () => {
      imageStore.setEditImage(null)
    }
    
    const saveImage = (updatedImage) => {
      imageStore.updateImage(updatedImage.id, updatedImage)
      closeEdit()
      showEditSuccess.value = true
      setTimeout(() => {
        showEditSuccess.value = false
      }, 3000)
    }
    
    const deleteImage = (id) => {
      if (confirm('确定要删除这张图片吗？')) {
        imageStore.deleteImage(id)
        alert('图片删除成功！')
      }
    }
    
    const applyFilterToImage = (id, filterType) => {
      imageStore.applyFilter(id, filterType)
    }
    
    const applyFilterToCurrentImage = (filterType) => {
      if (currentPreviewImage.value) {
        imageStore.applyFilter(currentPreviewImage.value.id, filterType)
      }
    }
    
    const showStorageInfo = () => {
      const totalImages = imageStore.getImages.length
      alert(`当前共有 ${totalImages} 张图片\n数据已保存到浏览器本地存储中\n刷新页面或重新打开浏览器不会丢失数据`)
    }
    
    // 监听上传成功事件
    const handleUploadSuccess = () => {
      showUploadSuccess.value = true
      setTimeout(() => {
        showUploadSuccess.value = false
      }, 3000)
    }
    
    return {
      viewMode,
      showUploadSuccess,
      showEditSuccess,
      categories,
      selectedCategory,
      filteredImages,
      currentPreviewImage,
      currentEditImage,
      selectCategory,
      previewImage,
      closePreview,
      editImage,
      editCurrentImage,
      closeEdit,
      saveImage,
      deleteImage,
      applyFilterToImage,
      applyFilterToCurrentImage,
      showStorageInfo,
      handleUploadSuccess
    }
  }
}
</script>

