<template>
  <div class="modal-overlay" @click.self="close">
    <div class="modal-container">
      <div class="modal-header">
        <h2>编辑图片信息</h2>
        <button class="close-btn" @click="close">
          <i class="fas fa-times"></i>
        </button>
      </div>
      
      <div class="modal-content">
        <div class="edit-preview">
          <img 
            :src="localImage.url" 
            :alt="localImage.title"
            :class="['edit-image', localImage.filter]"
          />
        </div>
        
        <form @submit.prevent="save" class="edit-form">
          <div class="form-group">
            <label for="title">标题</label>
            <input 
              type="text" 
              id="title" 
              v-model="localImage.title"
              placeholder="输入图片标题"
              required
            >
          </div>
          
          <div class="form-group">
            <label for="description">描述</label>
            <textarea 
              id="description" 
              v-model="localImage.description"
              placeholder="输入图片描述"
              rows="3"
            ></textarea>
          </div>
          
          <div class="form-group">
            <label for="category">分类</label>
            <select id="category" v-model="localImage.category">
              <option v-for="category in categories" :key="category" :value="category">
                {{ category }}
              </option>
            </select>
          </div>
          
          <div class="form-group">
            <label>标签</label>
            <div class="tags-input">
              <div class="tags-list">
                <span v-for="(tag, index) in localImage.tags" :key="index" class="tag">
                  {{ tag }}
                  <button type="button" class="remove-tag" @click="removeTag(index)">
                    <i class="fas fa-times"></i>
                  </button>
                </span>
              </div>
              <div class="tag-input-container">
                <input 
                  type="text" 
                  v-model="newTag"
                  placeholder="输入标签后按Enter"
                  @keydown.enter.prevent="addTag"
                >
                <button type="button" class="add-tag-btn" @click="addTag">
                  <i class="fas fa-plus"></i>
                </button>
              </div>
            </div>
          </div>
          
          <div class="form-group">
            <label>滤镜效果</label>
            <div class="filter-options">
              <div 
                v-for="filter in filters" 
                :key="filter.value"
                :class="['filter-option', { active: localImage.filter === filter.value }]"
                @click="selectFilter(filter.value)"
              >
                <div :class="['filter-preview', filter.value]"></div>
                <span>{{ filter.label }}</span>
              </div>
            </div>
          </div>
          
          <div class="form-group">
            <label>更换图片</label>
            <div class="image-upload-area" @click="triggerFileInput">
              <input 
                type="file" 
                ref="fileInput"
                @change="handleImageUpload"
                accept="image/*"
                style="display: none;"
              >
              <i class="fas fa-cloud-upload-alt"></i>
              <p>点击上传新图片</p>
              <small>支持 JPG, PNG, GIF 格式</small>
            </div>
          </div>
        </form>
      </div>
      
      <div class="modal-footer">
        <button type="button" class="btn-secondary" @click="close">
          取消
        </button>
        <button type="submit" class="btn-primary" @click="save">
          保存更改
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed } from 'vue'
import { useImageStore } from '../stores/imageStore'

export default {
  name: 'ImageEditModal',
  props: {
    image: {
      type: Object,
      required: true
    }
  },
  emits: ['close', 'save'],
  setup(props, { emit }) {
    const imageStore = useImageStore()
    
    // 创建本地副本以便编辑
    const localImage = ref({ ...props.image })
    const newTag = ref('')
    const fileInput = ref(null)
    
    const categories = computed(() => imageStore.categories.filter(cat => cat !== '全部'))
    
    const filters = [
      { label: '无滤镜', value: 'none' },
      { label: '灰度', value: 'grayscale' },
      { label: '复古', value: 'sepia' },
      { label: '模糊', value: 'blur' },
      { label: '亮度', value: 'brightness' },
      { label: '对比度', value: 'contrast' }
    ]
    
    const close = () => {
      emit('close')
    }
    
    const save = () => {
      emit('save', localImage.value)
    }
    
    const addTag = () => {
      if (newTag.value.trim() && !localImage.value.tags.includes(newTag.value.trim())) {
        localImage.value.tags.push(newTag.value.trim())
        newTag.value = ''
      }
    }
    
    const removeTag = (index) => {
      localImage.value.tags.splice(index, 1)
    }
    
    const selectFilter = (filterType) => {
      localImage.value.filter = filterType
    }
    
    const triggerFileInput = () => {
      fileInput.value.click()
    }
    
    const handleImageUpload = (event) => {
      const file = event.target.files[0]
      if (file) {
        if (file.type.startsWith('image/')) {
          const reader = new FileReader()
          reader.onload = (e) => {
            localImage.value.url = e.target.result
          }
          reader.readAsDataURL(file)
        } else {
          alert('请选择图片文件')
        }
      }
    }
    
    return {
      localImage,
      newTag,
      fileInput,
      categories,
      filters,
      close,
      save,
      addTag,
      removeTag,
      selectFilter,
      triggerFileInput,
      handleImageUpload
    }
  }
}
</script>

