<template>
  <div class="modal-overlay" @click.self="close">
    <div class="modal-container">
      <div class="modal-header">
        <h2>上传图片</h2>
        <button class="close-btn" @click="close">
          <i class="fas fa-times"></i>
        </button>
      </div>
      
      <div class="modal-content">
        <div class="upload-area" 
          @click="triggerFileInput"
          @dragover="handleDragOver"
          @drop="handleDrop"
          :class="{ 'has-image': imageFile, 'dragover': isDragging }"
        >
          <input 
            type="file" 
            ref="fileInput"
            @change="handleFileSelect"
            accept="image/*"
            style="display: none;"
          >
          
          <div v-if="!imageFile" class="upload-prompt">
            <i class="fas fa-cloud-upload-alt"></i>
            <h3>点击或拖拽图片到这里</h3>
            <p>支持 JPG, PNG, GIF 格式，建议尺寸 800x600</p>
            <p class="upload-tip">图片将保存到浏览器本地存储中</p>
          </div>
          
          <div v-else class="image-preview">
            <img :src="previewUrl" alt="预览" class="preview-image">
            <div class="image-info">
              <p>{{ imageFile.name }}</p>
              <p>{{ (imageFile.size / 1024).toFixed(2) }} KB</p>
            </div>
            <button class="remove-btn" @click.stop="removeImage">
              <i class="fas fa-trash"></i>
            </button>
          </div>
        </div>
        
        <div v-if="uploadProgress > 0" class="upload-progress">
          <div class="progress-bar">
            <div class="progress-fill" :style="{ width: uploadProgress + '%' }"></div>
          </div>
          <div class="progress-text">{{ uploadProgress }}%</div>
        </div>
        
        <form @submit.prevent="uploadImage" class="upload-form">
          <div class="form-group">
            <label for="upload-title">标题 *</label>
            <input 
              type="text" 
              id="upload-title" 
              v-model="imageData.title"
              placeholder="输入图片标题"
              required
            >
          </div>
          
          <div class="form-group">
            <label for="upload-description">描述</label>
            <textarea 
              id="upload-description" 
              v-model="imageData.description"
              placeholder="输入图片描述"
              rows="3"
            ></textarea>
          </div>
          
          <div class="form-group">
            <label for="upload-category">分类 *</label>
            <select id="upload-category" v-model="imageData.category" required>
              <option value="" disabled>选择分类</option>
              <option v-for="category in categories" :key="category" :value="category">
                {{ category }}
              </option>
            </select>
          </div>
          
          <div class="form-group">
            <label>标签</label>
            <div class="tags-input">
              <div class="tags-list">
                <span v-for="(tag, index) in imageData.tags" :key="index" class="tag">
                  {{ tag }}
                  <button type="button" class="remove-tag" @click="removeTag(index)">
                    <i class="fas fa-times"></i>
                  </button>
                </span>
              </div>
              <div class="tag-input-container">
                <input 
                  type="text" 
                  v-model="newTag"
                  placeholder="输入标签后按Enter"
                  @keydown.enter.prevent="addTag"
                >
                <button type="button" class="add-tag-btn" @click="addTag">
                  <i class="fas fa-plus"></i>
                </button>
              </div>
            </div>
          </div>
        </form>
      </div>
      
      <div class="modal-footer">
        <button type="button" class="btn-secondary" @click="close">
          取消
        </button>
        <button type="submit" class="btn-primary" @click="uploadImage" :disabled="!imageFile || isUploading">
          <i class="fas fa-upload"></i> 
          {{ isUploading ? '上传中...' : '上传图片' }}
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed } from 'vue'
import { useImageStore } from '../stores/imageStore'

export default {
  name: 'ImageUploadModal',
  emits: ['close', 'upload-success'],
  setup(props, { emit }) {
    const imageStore = useImageStore()
    
    const fileInput = ref(null)
    const imageFile = ref(null)
    const previewUrl = ref('')
    const newTag = ref('')
    const isDragging = ref(false)
    const uploadProgress = ref(0)
    const isUploading = ref(false)
    
    const imageData = ref({
      title: '',
      description: '',
      category: '自然',
      tags: [],
      url: ''
    })
    
    const categories = computed(() => imageStore.categories.filter(cat => cat !== '全部'))
    
    const close = () => {
      emit('close')
    }
    
    const triggerFileInput = () => {
      fileInput.value.click()
    }
    
    const handleFileSelect = (event) => {
      const file = event.target.files[0]
      processImageFile(file)
    }
    
    const processImageFile = (file) => {
      if (file) {
        if (!file.type.startsWith('image/')) {
          alert('请选择图片文件（JPG、PNG、GIF）')
          return
        }
        
        if (file.size > 10 * 1024 * 1024) {
          alert('图片大小不能超过10MB')
          return
        }
        
        imageFile.value = file
        
        // 创建预览URL
        const reader = new FileReader()
        reader.onload = (e) => {
          previewUrl.value = e.target.result
          imageData.value.url = e.target.result
        }
        reader.readAsDataURL(file)
        
        // 使用文件名作为默认标题
        if (!imageData.value.title) {
          const fileName = file.name.split('.').slice(0, -1).join('.')
          imageData.value.title = fileName.replace(/[_-]/g, ' ')
        }
      }
    }
    
    const removeImage = () => {
      imageFile.value = null
      previewUrl.value = ''
      imageData.value.url = ''
    }
    
    const addTag = () => {
      if (newTag.value.trim() && !imageData.value.tags.includes(newTag.value.trim())) {
        imageData.value.tags.push(newTag.value.trim())
        newTag.value = ''
      }
    }
    
    const removeTag = (index) => {
      imageData.value.tags.splice(index, 1)
    }
    
    const handleDragOver = (event) => {
      event.preventDefault()
      isDragging.value = true
      event.dataTransfer.dropEffect = 'copy'
    }
    
    const handleDragLeave = () => {
      isDragging.value = false
    }
    
    const handleDrop = (event) => {
      event.preventDefault()
      isDragging.value = false
      const file = event.dataTransfer.files[0]
      if (file) {
        processImageFile(file)
      }
    }
    
    const uploadImage = async () => {
      if (!imageFile.value) {
        alert('请选择要上传的图片')
        return
      }
      
      if (!imageData.value.title.trim()) {
        alert('请输入图片标题')
        return
      }
      
      if (!imageData.value.category) {
        alert('请选择图片分类')
        return
      }
      
      isUploading.value = true
      uploadProgress.value = 0
      
      // 模拟上传进度
      const interval = setInterval(() => {
        if (uploadProgress.value < 90) {
          uploadProgress.value += 10
        }
      }, 100)
      
      try {
        // 在实际应用中，这里会上传到服务器
        // 这里我们只是延迟一下模拟上传过程
        await new Promise(resolve => setTimeout(resolve, 1500))
        
        clearInterval(interval)
        uploadProgress.value = 100
        
        // 给用户一点时间看到上传完成
        setTimeout(() => {
          emit('upload-success', imageData.value)
          isUploading.value = false
          uploadProgress.value = 0
          
          // 重置表单
          resetForm()
        }, 300)
        
      } catch (error) {
        clearInterval(interval)
        isUploading.value = false
        uploadProgress.value = 0
        alert('上传失败，请重试')
        console.error('Upload error:', error)
      }
    }
    
    const resetForm = () => {
      imageFile.value = null
      previewUrl.value = ''
      newTag.value = ''
      imageData.value = {
        title: '',
        description: '',
        category: '自然',
        tags: [],
        url: ''
      }
      if (fileInput.value) {
        fileInput.value.value = ''
      }
    }
    
    return {
      fileInput,
      imageFile,
      previewUrl,
      newTag,
      imageData,
      categories,
      isDragging,
      uploadProgress,
      isUploading,
      close,
      triggerFileInput,
      handleFileSelect,
      removeImage,
      addTag,
      removeTag,
      handleDragOver,
      handleDragLeave,
      handleDrop,
      uploadImage
    }
  }
}
</script>

