<template>
  <div class="data-management">
    <button class="management-btn" @click="toggleDropdown">
      <i class="fas fa-database"></i> 数据管理
    </button>
    
    <div v-if="showDropdown" class="dropdown-menu">
      <div class="dropdown-item" @click="exportData">
        <i class="fas fa-download"></i>
        <span>导出数据</span>
      </div>
      <div class="dropdown-item" @click="triggerImport">
        <i class="fas fa-upload"></i>
        <span>导入数据</span>
      </div>
      <div class="dropdown-item" @click="clearData" style="color: #e53e3e;">
        <i class="fas fa-trash"></i>
        <span>清除所有数据</span>
      </div>
    </div>
    
    <input 
      type="file" 
      ref="importInput"
      @change="importData"
      accept=".json"
      style="display: none;"
    />
  </div>
</template>

<script>
import { ref } from 'vue'
import { useImageStore } from '../stores/imageStore'

export default {
  name: 'DataManagement',
  setup() {
    const imageStore = useImageStore()
    const showDropdown = ref(false)
    const importInput = ref(null)
    
    const toggleDropdown = () => {
      showDropdown.value = !showDropdown.value
    }
    
    // 点击其他地方关闭下拉菜单
    const closeDropdown = (event) => {
      if (!event.target.closest('.data-management')) {
        showDropdown.value = false
      }
    }
    
    // 添加全局点击监听
    document.addEventListener('click', closeDropdown)
    
    const exportData = () => {
      imageStore.exportData()
      showDropdown.value = false
      alert('数据已导出为JSON文件')
    }
    
    const triggerImport = () => {
      importInput.value.click()
      showDropdown.value = false
    }
    
    const importData = (event) => {
      const file = event.target.files[0]
      if (!file) return
      
      const reader = new FileReader()
      reader.onload = (e) => {
        const result = imageStore.importData(e.target.result)
        if (result) {
          alert('数据导入成功！')
        } else {
          alert('数据导入失败，请检查文件格式')
        }
      }
      reader.readAsText(file)
      
      // 重置文件输入
      event.target.value = ''
    }
    
    const clearData = () => {
      if (confirm('确定要清除所有数据吗？这将恢复为默认图片数据。')) {
        imageStore.clearAllData()
        showDropdown.value = false
        alert('数据已清除，已恢复默认图片')
      }
    }
    
    return {
      showDropdown,
      importInput,
      toggleDropdown,
      exportData,
      triggerImport,
      importData,
      clearData
    }
  }
}
</script>

