<template>
  <div class="modal-overlay" @click.self="close">
    <div class="modal-container">
      <div class="modal-header">
        <h2>{{ image.title }}</h2>
        <button class="close-btn" @click="close">
          <i class="fas fa-times"></i>
        </button>
      </div>
      
      <div class="modal-content">
        <div class="image-preview">
          <img 
            :src="image.url" 
            :alt="image.title"
            :class="['preview-image', image.filter]"
          />
        </div>
        
        <div class="image-details">
          <div class="detail-row">
            <label>描述：</label>
            <p>{{ image.description }}</p>
          </div>
          <div class="detail-row">
            <label>分类：</label>
            <span class="category-tag">{{ image.category }}</span>
          </div>
          <div class="detail-row">
            <label>标签：</label>
            <div class="tags">
              <span v-for="tag in image.tags" :key="tag" class="tag">
                {{ tag }}
              </span>
            </div>
          </div>
          <div class="detail-row">
            <label>上传时间：</label>
            <span>{{ image.createdAt }}</span>
          </div>
        </div>
        
        <div class="filter-controls">
          <h3>应用滤镜</h3>
          <div class="filter-options">
            <button 
              v-for="filter in filters" 
              :key="filter.value"
              :class="['filter-btn', { active: image.filter === filter.value }]"
              @click="applyFilter(filter.value)"
            >
              {{ filter.label }}
            </button>
          </div>
        </div>
      </div>
      
      <div class="modal-footer">
        <button class="btn-secondary" @click="edit">
          <i class="fas fa-edit"></i> 编辑图片
        </button>
        <button class="btn-primary" @click="close">
          关闭
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ImagePreviewModal',
  props: {
    image: {
      type: Object,
      required: true
    }
  },
  emits: ['close', 'edit', 'apply-filter'],
  setup(props, { emit }) {
    const filters = [
      { label: '无滤镜', value: 'none' },
      { label: '灰度', value: 'grayscale' },
      { label: '复古', value: 'sepia' },
      { label: '模糊', value: 'blur' },
      { label: '亮度', value: 'brightness' },
      { label: '对比度', value: 'contrast' }
    ]
    
    const close = () => {
      emit('close')
    }
    
    const edit = () => {
      emit('edit')
    }
    
    const applyFilter = (filterType) => {
      emit('apply-filter', filterType)
    }
    
    return {
      filters,
      close,
      edit,
      applyFilter
    }
  }
}
</script>

