import { defineStore } from 'pinia'
import { ref, computed, watch } from 'vue'

export const useImageStore = defineStore('image', () => {
  // 从本地存储加载图片数据，如果没有则使用默认数据
  const loadImagesFromStorage = () => {
    const savedImages = localStorage.getItem('imageGalleryImages')
    if (savedImages) {
      try {
        return JSON.parse(savedImages)
      } catch (error) {
        console.error('Failed to parse images from localStorage:', error)
        return getDefaultImages()
      }
    }
    return getDefaultImages()
  }

  // 默认图片数据（使用可靠的图片地址）
  const getDefaultImages = () => [
    {
      id: 1,
      title: '自然风光',
      description: '美丽的山景和湖泊',
      category: '自然',
      url: '/images/山景湖泊.jpg',
      tags: ['山', '湖', '自然'],
      createdAt: '2026-1-01',
      filter: 'none'
    },
    {
      id: 2,
      title: '城市夜景',
      description: '繁华的城市灯光',
      category: '城市',
      url: '/images/城市夜景.jpg',
      tags: ['城市', '夜景', '建筑'],
      createdAt: '2026-01-02',
      filter: 'none'
    },
    {
      id: 3,
      title: '海滩日落',
      description: '宁静的海滩日落景色',
      category: '自然',
      url: 'https://images.unsplash.com/photo-1507525428034-b723cf961d3e?ixlib=rb-4.0.3&auto=format&fit=crop&w=800&h=600&q=80',
      tags: ['海滩', '日落', '海洋'],
      createdAt: '2026-01-03',
      filter: 'none'
    },
    {
      id: 4,
      title: '森林小径',
      description: '阳光穿过森林的小径',
      category: '自然',
      url: 'https://images.unsplash.com/photo-1441974231531-c6227db76b6e?ixlib=rb-4.0.3&auto=format&fit=crop&w=800&h=600&q=80',
      tags: ['森林', '树木', '小径'],
      createdAt: '2026-01-04',
      filter: 'none'
    },
    {
      id: 5,
      title: '现代建筑',
      description: '当代建筑设计',
      category: '建筑',
      url: '/images/现代建筑.jpg',
      tags: ['建筑', '现代', '设计'],
      createdAt: '2026-01-05',
      filter: 'none'
    },
    {
      id: 6,
      title: '美食摄影',
      description: '美味的食物特写',
      category: '食物',
      url: 'https://images.unsplash.com/photo-1565958011703-44f9829ba187?ixlib=rb-4.0.3&auto=format&fit=crop&w=800&h=600&q=80',
      tags: ['食物', '美食', '摄影'],
      createdAt: '2026-01-06',
      filter: 'none'
    },
    {
      id: 7,
      title: '动物世界',
      description: '可爱的野生动物',
      category: '动物',
      url: 'https://images.unsplash.com/photo-1557050543-4d5f4e07ef46?ixlib=rb-4.0.3&auto=format&fit=crop&w=800&h=600&q=80',
      tags: ['动物', '野生动物', '自然'],
      createdAt: '2026-01-07',
      filter: 'none'
    },
    {
      id: 8,
      title: '抽象艺术',
      description: '现代抽象艺术作品',
      category: '艺术',
      url: 'https://images.unsplash.com/photo-1541961017774-22349e4a1262?ixlib=rb-4.0.3&auto=format&fit=crop&w=800&h=600&q=80',
      tags: ['艺术', '抽象', '创意'],
      createdAt: '2026-01-08',
      filter: 'none'
    },
    {
      id: 9,
      title: '冬季雪景',
      description: '白雪覆盖的冬季景观',
      category: '自然',
      url: '/images/冬季景观.jpg',
      tags: ['雪', '冬季', '寒冷'],
      createdAt: '2026-01-09',
      filter: 'none'
    },
    {
      id: 10,
      title: '科技未来',
      description: '科技与未来的概念',
      category: '科技',
      url: '/images/科技未来.jpg',
      tags: ['科技', '未来', '数字'],
      createdAt: '2026-01-10',
      filter: 'none'
    }
  ]

  // 初始图片数据
  const images = ref(loadImagesFromStorage())

  // 保存图片数据到本地存储
  const saveImagesToStorage = () => {
    try {
      localStorage.setItem('imageGalleryImages', JSON.stringify(images.value))
    } catch (error) {
      console.error('Failed to save images to localStorage:', error)
    }
  }

  // 监听images变化，自动保存到本地存储
  watch(
    images,
    () => {
      saveImagesToStorage()
    },
    { deep: true }
  )

  // 所有分类
  const categories = ref(['全部', '自然', '城市', '建筑', '食物', '动物', '艺术', '科技', '人物', '旅行'])

  // 当前选中的分类
  const selectedCategory = ref('全部')

  // 当前预览的图片
  const currentPreviewImage = ref(null)

  // 当前编辑的图片
  const currentEditImage = ref(null)

  // 过滤后的图片
  const filteredImages = computed(() => {
    if (selectedCategory.value === '全部') {
      return images.value
    }
    return images.value.filter(image => image.category === selectedCategory.value)
  })

  // 获取所有图片
  const getImages = computed(() => images.value)

  // 添加图片
  const addImage = (imageData) => {
    const newImage = {
      id: Date.now(),
      ...imageData,
      createdAt: new Date().toISOString().split('T')[0],
      filter: 'none'
    }
    images.value.unshift(newImage)
    // saveImagesToStorage() // watch会自动调用
  }

  // 更新图片
  const updateImage = (id, updates) => {
    const index = images.value.findIndex(img => img.id === id)
    if (index !== -1) {
      images.value[index] = { ...images.value[index], ...updates }
      // saveImagesToStorage() // watch会自动调用
    }
  }

  // 删除图片
  const deleteImage = (id) => {
    const index = images.value.findIndex(img => img.id === id)
    if (index !== -1) {
      images.value.splice(index, 1)
      // saveImagesToStorage() // watch会自动调用
    }
  }

  // 设置当前预览图片
  const setPreviewImage = (image) => {
    currentPreviewImage.value = image
  }

  // 清除预览图片
  const clearPreviewImage = () => {
    currentPreviewImage.value = null
  }

  // 设置当前编辑图片
  const setEditImage = (image) => {
    currentEditImage.value = image ? { ...image } : null
  }

  // 应用滤镜到图片
  const applyFilter = (id, filterType) => {
    const index = images.value.findIndex(img => img.id === id)
    if (index !== -1) {
      images.value[index].filter = filterType
      // saveImagesToStorage() // watch会自动调用
    }
  }

  // 清除所有数据（用于测试）
  const clearAllData = () => {
    images.value = getDefaultImages()
    localStorage.removeItem('imageGalleryImages')
  }

  // 导出数据（用于备份）
  const exportData = () => {
    const dataStr = JSON.stringify(images.value, null, 2)
    const dataUri = 'data:application/json;charset=utf-8,'+ encodeURIComponent(dataStr)
    
    const exportFileDefaultName = 'image-gallery-data.json'
    
    const linkElement = document.createElement('a')
    linkElement.setAttribute('href', dataUri)
    linkElement.setAttribute('download', exportFileDefaultName)
    linkElement.click()
  }

  // 导入数据
  const importData = (jsonData) => {
    try {
      const importedImages = JSON.parse(jsonData)
      if (Array.isArray(importedImages)) {
        images.value = importedImages
        return true
      }
    } catch (error) {
      console.error('Failed to import data:', error)
    }
    return false
  }

  return {
    images,
    categories,
    selectedCategory,
    currentPreviewImage,
    currentEditImage,
    filteredImages,
    getImages,
    addImage,
    updateImage,
    deleteImage,
    setPreviewImage,
    clearPreviewImage,
    setEditImage,
    applyFilter,
    clearAllData,
    exportData,
    importData
  }
})